import React, { Component } from 'react';
import Axios from "axios/index";
import Autocomplete from 'react-autocomplete';
import {Redirect} from 'react-router-dom';

export default class EditUser extends Component {
    constructor(props) {
        super(props);

        this.state = {
            redirect: false,
            isEditing: false,
            userId: null,
            cities: [],
            form: {
                name: {
                    value: '',
                    errors: []
                },
                login: {
                    value: '',
                    errors: []
                },
                email: {
                    value: '',
                    errors: []
                },
                password: {
                    value: '',
                    errors: []
                },
                priceA: {
                    value: '',
                    errors: []
                },
                priceB: {
                    value: '',
                    errors: []
                },
                priceC: {
                    value: '',
                    errors: []
                },
                priceD: {
                    value: '',
                    errors: []
                },
                priceE: {
                    value: '',
                    errors: []
                },
                city: {
                    value: '',
                    id: null,
                    errors: []
                },
                ips: {
                    value: '',
                    list: [],
                    errors: []
                }
            }
        }
    }

    componentWillMount() {
        Axios.get(process.env.REACT_APP_API_HOST + 'api/cities?apiToken=' + this.props.apiToken)
            .then((response) => {
                this.setState({
                    cities: response.data.data.cities
                })
            });

        if (this.props.userId) {
            this.setState({
                isEditing: true,
                userId: this.props.userId
            });

            Axios.get(process.env.REACT_APP_API_HOST + 'api/agents/' + this.props.userId + '?apiToken=' + this.props.apiToken)
                .then((response) => {
                    if (response.data.success) {
                        let agent = response.data.data.agent;

                        for (let key in agent) {
                            if (key == 'city') {
                                this.setState({
                                    form: {
                                        ...this.state.form,
                                        [key]: {
                                            value: agent[key].name,
                                            id: agent[key].id,
                                            errors: [],
                                        }
                                    }
                                })
                            } else if (key == 'ips') {
                                let ipsString = '';

                                agent.ips.forEach((ip) => {
                                    ipsString += `${ip}\n`;
                                });

                                this.setState({
                                    form: {
                                        ...this.state.form,
                                        [key]: {
                                            value: ipsString,
                                            list: agent[key],
                                            errors: [],
                                        }
                                    }
                                })
                            } else {
                                this.setState({
                                    form: {
                                        ...this.state.form,
                                        [key]: {
                                            value: agent[key],
                                            errors: [],
                                        }
                                    }
                                })
                            }
                        }
                    }
                });
        }
    }

    handleFormSubmit(event) {
        event.preventDefault();

        let data = {
            name: this.state.form.name.value,
            login: this.state.form.login.value,
            email: this.state.form.email.value,
            password: this.state.form.password.value,
            priceA: this.state.form.priceA.value,
            priceB: this.state.form.priceB.value,
            priceC: this.state.form.priceC.value,
            priceD: this.state.form.priceD.value,
            priceE: this.state.form.priceE.value,
            city: this.state.form.city.id,
            ips: this.state.form.ips.list
        };

        if (this.state.userId) {
            data = {
                id: this.state.userId,
                name: this.state.form.name.value,
                email: this.state.form.email.value,
                priceA: this.state.form.priceA.value,
                priceB: this.state.form.priceB.value,
                priceC: this.state.form.priceC.value,
                priceD: this.state.form.priceD.value,
                priceE: this.state.form.priceE.value,
                city: this.state.form.city.id,
                ips: this.state.form.ips.list
            }
        }

        Axios.post(process.env.REACT_APP_API_HOST + 'api/users?apiToken=' + this.props.apiToken, data)
            .then((response) => {
                for (let key in this.state.form) {
                    this.setState({
                        form: {
                            ...this.state.form,
                            [key]: {
                                ...this.state.form[key],
                                errors: []
                            }
                        }
                    })
                }

                if (response.data.success) {
                    this.setState({
                        redirect: true
                    })
                } else {
                    if (response.data.data.errors) {
                        for (let key in response.data.data.errors) {
                            this.setState({
                                form: {
                                    ...this.state.form,
                                    [key]: {
                                        ...this.state.form[key],
                                        errors: response.data.data.errors[key]
                                    }
                                }
                            })
                        }
                    }
                }
            })
    }

    showErrors(fieldName) {
        return this.state.form[fieldName].errors.map((error) => {
            return (
                <p className="help is-danger">{error}</p>
            )
        })
    }

    applyInputClass(fieldName) {
        return (this.state.form[fieldName].errors.length > 0) ? 'input is-danger' : 'input';
    }

    applyTextareaClass(fieldName) {
        return (this.state.form[fieldName].errors.length > 0) ? 'textarea is-danger' : 'textarea';
    }

    handleInput(event, fieldName) {
        this.setState({
            form: {
                ...this.state.form,
                [fieldName]: {
                    ...this.state.form[fieldName],
                    value: event.target.value,
                    errors: []
                }
            }
        })
    }

    handleIpsChange(event) {
        let ips = event.target.value.split(/\r\n|\r|\n/g);

        this.setState({
            form: {
                ...this.state.form,
                ips: {
                    value: event.target.value,
                    list: ips,
                    errors: [],
                }
            }
        })
    }

    handleCityChange(event) {
        this.setState({
            form: {
                ...this.state.form,
                city: {
                    ...this.state.form.city,
                    value: event.target.value,
                    errors: []
                }
            }
        });

        Axios.get(process.env.REACT_APP_API_HOST + 'api/cities?apiToken=' + this.props.apiToken + '&q=' + event.target.value)
            .then((response) => {
                console.log(response.data.data.cities);

                this.setState({
                    cities: response.data.data.cities
                })
            });
    }

    render() {
        if (this.state.redirect) {
            return (
                <Redirect to="/users"/>
            )
        }

        const passwordField = (!this.state.isEditing) ? (
            <div className="field">
                <label className="label">Пароль</label>
                <div className="control">
                    <input
                        value={this.state.form.password.value}
                        onChange={(event) => this.handleInput(event, 'password')}
                        className={this.applyInputClass('password')}
                        type="password" />
                </div>
                {this.showErrors('password')}
            </div>
        ) : null;

        return (
            <div className="column is-9">
                <div className="level">
                    <span className="is-size-3">Добавить агента</span>
                </div>
                <div className="columns">
                    <div className="column is-6">
                        <form onSubmit={(event) => this.handleFormSubmit(event)}>
                            <div className="field">
                                <label className="label">Имя пользователя</label>
                                <div className="control">
                                    <input
                                        value={this.state.form.name.value}
                                        onChange={(event) => this.handleInput(event, 'name')}
                                        className={this.applyInputClass('name')}
                                        type="text" />
                                </div>
                                {this.showErrors('name')}
                            </div>
                            <div className="field">
                                <label className="label">Логин</label>
                                <div className="control">
                                    <input
                                        value={this.state.form.login.value}
                                        onChange={(event) => this.handleInput(event, 'login')}
                                        className={this.applyInputClass('login')}
                                        type="text" disabled={this.state.isEditing} />
                                </div>
                                {this.showErrors('login')}
                            </div>
                            {passwordField}
                            <div className="field">
                                <label className="label">Email</label>
                                <div className="control">
                                    <input
                                        value={this.state.form.email.value}
                                        onChange={(event) => this.handleInput(event, 'email')}
                                        className={this.applyInputClass('email')}
                                        type="text" />
                                </div>
                                {this.showErrors('email')}
                            </div>
                            <div className="field">
                                <label className="label">Разрешенные IP-адреса</label>
                                <div className="control">
                                            <textarea
                                                value={this.state.form.ips.value}
                                                onChange={(event) => this.handleIpsChange(event)}
                                                className={this.applyTextareaClass('ips')}
                                                rows="3" />
                                </div>
                                {this.showErrors('ips')}
                            </div>
                            <div className="field">
                                <label className="label">Город</label>
                                <div className="control">
                                    <Autocomplete
                                        items={this.state.cities}
                                        shouldItemRender={(item, value) => item.name.toLowerCase().indexOf(value.toLowerCase()) > -1}
                                        getItemValue={item => item.name}
                                        renderItem={(item, highlighted) =>
                                            <div
                                                key={item.id}
                                                style={{ backgroundColor: highlighted ? '#eee' : 'transparent'}}
                                            >
                                                {item.name} ({(item.ruPath != '') ? item.ruPath: item.name})
                                            </div>
                                        }
                                        inputProps={{
                                            className: this.applyInputClass('city')
                                        }}
                                        menuStyle={
                                            {
                                                background: 'rgba(255, 255, 255)',
                                                border: '1px solid black'
                                            }
                                        }
                                        wrapperStyle={
                                            {
                                                display: 'block'
                                            }
                                        }
                                        value={this.state.form.city.value}
                                        onChange={(event) => this.handleCityChange(event)}
                                        onSelect={
                                            (value, item) => {
                                                this.setState({
                                                    form: {
                                                        ...this.state.form,
                                                        city: {
                                                            id: item.id,
                                                            value: item.name,
                                                            errors: []
                                                        }
                                                    }
                                                })
                                            }
                                        }
                                    />
                                </div>
                                {this.showErrors('city')}
                            </div>
                            <div className="field">
                                <label className="label">Цены</label>
                                <div className="control">
                                    <table className="table is-fullwidth is-striped">
                                        <tr>
                                            <td>
                                                <div className="field">
                                                    <label className="label">A</label>
                                                    <div className="control">
                                                        <input
                                                            value={this.state.form.priceA.value}
                                                            onChange={(event) => this.handleInput(event, 'priceA')}
                                                            className={this.applyInputClass('priceA')}
                                                            type="text" />
                                                    </div>
                                                </div>
                                            </td>
                                            <td>
                                                <div className="field">
                                                    <label className="label">B</label>
                                                    <div className="control">
                                                        <input
                                                            value={this.state.form.priceB.value}
                                                            onChange={(event) => this.handleInput(event, 'priceB')}
                                                            className={this.applyInputClass('priceB')}
                                                            type="text" />
                                                    </div>
                                                </div>
                                            </td>
                                            <td>
                                                <div className="field">
                                                    <label className="label">C</label>
                                                    <div className="control">
                                                        <input
                                                            value={this.state.form.priceC.value}
                                                            onChange={(event) => this.handleInput(event, 'priceC')}
                                                            className={this.applyInputClass('priceC')}
                                                            type="text" />
                                                    </div>
                                                </div>
                                            </td>
                                            <td>
                                                <div className="field">
                                                    <label className="label">D</label>
                                                    <div className="control">
                                                        <input
                                                            value={this.state.form.priceD.value}
                                                            onChange={(event) => this.handleInput(event, 'priceD')}
                                                            className={this.applyInputClass('priceD')}
                                                            type="text" />
                                                    </div>
                                                </div>
                                            </td>
                                            <td>
                                                <div className="field">
                                                    <label className="label">E</label>
                                                    <div className="control">
                                                        <input
                                                            value={this.state.form.priceE.value}
                                                            onChange={(event) => this.handleInput(event, 'priceE')}
                                                            className={this.applyInputClass('priceE')}
                                                            type="text" />
                                                    </div>
                                                </div>
                                            </td>
                                        </tr>
                                    </table>
                                </div>
                                {this.showErrors('priceA')}
                                {this.showErrors('priceB')}
                                {this.showErrors('priceC')}
                                {this.showErrors('priceD')}
                                {this.showErrors('priceE')}
                            </div>
                            <div className="field is-grouped">
                                <div className="control">
                                    <button className="button is-link">Сохранить</button>
                                </div>
                                <div className="control">
                                    <button className="button is-text">Отмена</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}


