import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Axios from "axios/index";
import UserTableItem from "./UserTableItem";

export default class Users extends Component {
    constructor(props) {
        super(props);

        this.state = {
            users: [],
            showOnlyActive: false
        }
    }

    handleCheckboxChange() {
        this.setState({
            showOnlyActive: !this.state.showOnlyActive
        })
    }

    countBannedUsers() {
        return this.state.users.filter((user) => {
            return !user.isActive;
        }).length;
    }

    componentWillMount() {
        Axios.get(process.env.REACT_APP_API_HOST + 'api/agents?excludeCurrent=1&apiToken=' + this.props.apiToken)
            .then((response) => {
                if (response.data.success) {
                    const users = response.data.data.agents.map((user) => {
                        if (user.isActive === '1') {
                            user.isActive = true;
                        }

                        if (user.isActive === '0') {
                            user.isActive = false;
                        }

                        return user;
                    });

                    this.setState({
                        users: response.data.data.agents
                    })
                }
            });
    }

    handleUserStatus(id, isActive) {
        Axios.post(process.env.REACT_APP_API_HOST + 'api/userStatus?apiToken=' + this.props.apiToken, {
            id: id,
            isActive: Number(isActive)
        })
            .then((response) => {
                if (response.data.success) {
                    const users = this.state.users.map((user) => {
                        if (user.id === id) {
                            user.isActive = isActive;
                        }

                        return user;
                    })

                    this.setState({
                        users: users
                    })
                }
            })
    }

    showUsers() {
        return this.state.users.map((user) => {
            if (!this.state.showOnlyActive) {
                return (
                    <UserTableItem
                        handleUserStatus={(isActive) => this.handleUserStatus(user.id, isActive)}
                        name={user.name}
                        id={user.id}
                        login={user.login}
                        email={user.email}
                        priceA={user.priceA}
                        priceB={user.priceB}
                        priceC={user.priceC}
                        priceD={user.priceD}
                        priceE={user.priceE}
                        isActive={user.isActive}
                        key={user.id}/>
                );
            } else {
                if (user.isActive) {
                    return (
                        <UserTableItem
                            handleUserStatus={(isActive) => this.handleUserStatus(user.id, isActive)}
                            name={user.name}
                            id={user.id}
                            login={user.login}
                            email={user.email}
                            priceA={user.priceA}
                            priceB={user.priceB}
                            priceC={user.priceC}
                            priceD={user.priceD}
                            priceE={user.priceE}
                            isActive={user.isActive}
                            key={user.id}/>
                    )
                }
            }
        });
    }

    render() {
        return (
            <div className="column is-9">
                <div className="level">
                    <span className="is-size-3">Зарегистрированные агенты:</span>
                </div>
                <div className="columns">
                    <div className="column is-2">
                        <Link className="button is-primary" to="/user">Добавить агента</Link>
                    </div>
                    <div className="column is-4">
                        <label className="checkbox">
                            <input type="checkbox" checked={this.state.showOnlyActive} onChange={() => this.handleCheckboxChange()} />
                            <strong>Скрыть заблокированных агентов ({this.countBannedUsers()})</strong>
                        </label>
                    </div>
                </div>
                <div className="columns">
                    <div className="column is-12">
                        <table className="table is-fullwidth is-striped">
                            <thead>
                            <tr>
                                <th></th>
                                <th>Имя</th>
                                <th>Логин</th>
                                <th>Email</th>
                                <th>Цены</th>
                                <th>Статус</th>
                            </tr>
                            </thead>
                            <tbody>
                            {this.showUsers()}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        );
    }
}


