import React, { Component, Fragment } from 'react';
import CardTableItem from "./CardTableItem";
import { getCategoryCodes } from '../helpers/helperCategory';
import { createFullDateFromTimestamp, createDateFromTimestamp } from '../helpers/helperDateTime';
import Axios from "axios/index";
import Autocomplete from 'react-autocomplete';
import StatTableItem from "./StatTableItem";
import ReactTable from 'react-table';
import 'react-table/react-table.css'

export default class Cards extends Component {
    constructor(props) {
        super(props);

        this.state = {
            showOnlyActive: false,
            showOnlyWithDeals: false,
            countTotal: 0,
            itemsPerPage: 10,
            currentPage: 1,
            toUpdate: false,
            dateFrom: Date.now() / 1000 - 60 * 24 * 3600,
            dateTill: Date.now() / 1000,
            agentLoginFilter: '',
            agentIdFilter: null,
            cards: [],
            stat: [],
            agents: [],
        }
    }

    componentDidUpdate() {
        if (this.state.toUpdate) {
            this.applyFilters();
        }
    }

    componentWillMount() {
        this.fetchCards()
            .then((response) => {

                const cards = response.data.data.cards.map((card) => {
                    card.createdAt = new Date(card.createdAt);
                    card.validTill = new Date(card.validTill);
                    card.eaistoDate = new Date(card.eaistoDate);

                    return card;
                });

                this.setState({
                    cards: cards,
                    countTotal: Number(response.data.data.countTotal)
                })
            });

        this.fetchStat()
            .then((response) => {

                const stat = response.data.data.stat.map((statItem) => {
                    statItem.priceA = Number(statItem.priceA);
                    statItem.priceB = Number(statItem.priceB);
                    statItem.priceC = Number(statItem.priceC);
                    statItem.priceD = Number(statItem.priceD);
                    statItem.priceE = Number(statItem.priceE);

                    statItem.countA = Number(statItem.countA);
                    statItem.countB = Number(statItem.countB);
                    statItem.countC = Number(statItem.countC);
                    statItem.countD = Number(statItem.countD);
                    statItem.countE = Number(statItem.countE);

                    if (statItem.isActive === '0') {
                        statItem.isActive = false;
                    }

                    if (statItem.isActive === '1') {
                        statItem.isActive = true;
                    }

                    return statItem;
                });

                this.setState({
                    stat: stat
                })
            });

        this.fetchAgents()
            .then((response) => {
                this.setState({
                    agents: response.data.data.agents
                })
            });
    }

    fetchCards() {
        return Axios.get(process.env.REACT_APP_API_HOST + 'api/cards?apiToken=' + this.props.apiToken);
    }

    fetchStat() {
        return Axios.get(process.env.REACT_APP_API_HOST + 'api/stat?apiToken=' + this.props.apiToken);
    }

    fetchAgents() {
        return Axios.get(process.env.REACT_APP_API_HOST + 'api/agents?apiToken=' + this.props.apiToken);
    }

    handleDateFrom(event) {
        let date = new Date(event.target.value);

        this.setState({
            toUpdate: true,
            dateFrom: date.getTime() / 1000,
            currentPage: 1
        });
    }

    handleDateTill(event) {
        let date = new Date(event.target.value);

        this.setState({
            toUpdate: true,
            dateTill: date.getTime() / 1000,
            currentPage: 1
        });
    }

    applyFilters() {
        let statUrl = process.env.REACT_APP_API_HOST + `api/stat?dateFrom=${this.state.dateFrom}&dateTill=${this.state.dateTill}`;

        if (this.state.agentIdFilter !== null) {
            statUrl += `&userId=${this.state.agentIdFilter}`;
        }

        statUrl += `&apiToken=${this.props.apiToken}`;

        Axios.get(statUrl)
            .then((response) => {
                const stat = response.data.data.stat.map((statItem) => {
                    statItem.priceA = Number(statItem.priceA);
                    statItem.priceB = Number(statItem.priceB);
                    statItem.priceC = Number(statItem.priceC);
                    statItem.priceD = Number(statItem.priceD);
                    statItem.priceE = Number(statItem.priceE);

                    statItem.countA = Number(statItem.countA);
                    statItem.countB = Number(statItem.countB);
                    statItem.countC = Number(statItem.countC);
                    statItem.countD = Number(statItem.countD);
                    statItem.countE = Number(statItem.countE);

                    if (statItem.isActive === '0') {
                        statItem.isActive = false;
                    }

                    if (statItem.isActive === '1') {
                        statItem.isActive = true;
                    }

                    return statItem;
                });

                this.setState({
                    toUpdate: false,
                    stat: stat
                })
            });

        const limit = this.state.itemsPerPage;
        const offset = (this.state.currentPage - 1) * limit;

        let cardsUrl = process.env.REACT_APP_API_HOST + `api/cards?dateFrom=${this.state.dateFrom}&dateTill=${this.state.dateTill}&limit=${limit}&offset=${offset}`;

        if (this.state.agentIdFilter !== null) {
            cardsUrl += `&userId=${this.state.agentIdFilter}`;
        }

        cardsUrl += `&apiToken=${this.props.apiToken}`;

        Axios.get(cardsUrl)
            .then((response) => {

                const cards = response.data.data.cards.map((card) => {
                    card.createdAt = new Date(card.createdAt);
                    card.validTill = new Date(card.validTill);
                    card.eaistoDate = new Date(card.eaistoDate);

                    return card;
                });

                this.setState({
                    toUpdate: false,
                    cards: cards,
                    countTotal: Number(response.data.data.countTotal)
                })
            });
    }

    countBannedUsers() {
        return this.state.stat.filter((statItem) => {
            return statItem.isActive === false;
        }).length;
    }

    countWithoutDealsUsers() {
        return this.state.stat.filter((statItem) => {
            return statItem.countA + statItem.countB + statItem.countC + statItem.countD + statItem.countE === 0;
        }).length;
    }

    handleItemsPerPage(itemsPerPage) {
        this.setState({
            itemsPerPage: itemsPerPage,
            currentPage: 1,
            toUpdate: true
        })
    }

    handleCurrentPage(currentPage) {
        this.setState({
            currentPage: currentPage,
            toUpdate: true
        })
    }

    applyItemsPerPageClassName(itemsPerPage) {
        let className = 'pagination-link';

        if (this.state.itemsPerPage === itemsPerPage) {
            className += ' is-current';
        }

        return className;
    }

    applyCurrentPageClassName(currentPage) {
        let className = 'pagination-link';

        if (this.state.currentPage === currentPage) {
            className += ' is-current';
        }

        return className;
    }

    populatePaginationPositionSlot(position) {
        if (this.state.currentPage < 5 || this.countPages() < 7) {
            return position;
        } else if (this.countPages() - this.state.currentPage >= 3) {
            if (position === 7) {
                return this.state.currentPage + 3;
            }

            if (position === 6) {
                return this.state.currentPage + 2;
            }

            if (position === 5) {
                return this.state.currentPage + 1;
            }

            if (position === 4) {
                return this.state.currentPage;
            }

            if (position === 3) {
                return this.state.currentPage - 1;
            }

            if (position === 2) {
                return this.state.currentPage - 2;
            }

            if (position === 1) {
                return this.state.currentPage - 3;
            }
        } else {
            if (position === 7) {
                return this.countPages();
            }

            if (position === 6) {
                return this.countPages() - 1;
            }

            if (position === 5) {
                return this.countPages() - 2;
            }

            if (position === 4) {
                return this.countPages() - 3;
            }

            if (position === 3) {
                return this.countPages() - 4;
            }

            if (position === 2) {
                return this.countPages() - 5;
            }

            if (position === 1) {
                return this.countPages() - 6;
            }
        }
    }

    countPages() {
        return Math.ceil(this.state.countTotal / this.state.itemsPerPage);
    }

    showPagination() {
        let back = null;
        let forward = null;

        if (this.state.currentPage > 1) {
            back = (
                <Fragment>
                    <li><a className="pagination-previous" onClick={() => this.handleCurrentPage(1)}>{'<<'}</a></li>
                    <li><a className="pagination-previous" onClick={() => this.handleCurrentPage(this.state.currentPage - 1)}>{'<'}</a></li>
                </Fragment>
            )
        }

        if (this.countPages() - this.state.currentPage > 3) {
            forward = (
                <Fragment>
                    <li><a className="pagination-next" onClick={() => this.handleCurrentPage(this.state.currentPage + 1)}>{'>'}</a></li>
                    <li><a className="pagination-next" onClick={() => this.handleCurrentPage(this.countPages())}>{'>>'}</a></li>
                </Fragment>
            )
        }

        let pages = [];

        if (this.countPages() <= 7) {
            for (let page = 1; page <= this.countPages(); page++) {
                pages.push(page);
            }
        } else {
            for (let page = 1; page <= 7; page++) {
                pages.push(page);
            }
        }

        let paginationLinks = pages.map((page) => {
            const pos = this.populatePaginationPositionSlot(page);

            return (
                <li key={page}><a className={this.applyCurrentPageClassName(pos)} onClick={() => this.handleCurrentPage(pos)}>{pos}</a></li>
            );
        });

        return (
            <div className="columns">
                <div className="column is-3">
                    <p><strong>Строк на странице:</strong></p>
                    <nav className="pagination is-rounded" role="navigation" aria-label="pagination">
                        <ul className="pagination-list">
                            <li><a className={this.applyItemsPerPageClassName(5)} onClick={() => this.handleItemsPerPage(5)}>5</a></li>
                            <li><a className={this.applyItemsPerPageClassName(10)} onClick={() => this.handleItemsPerPage(10)}>10</a></li>
                            <li><a className={this.applyItemsPerPageClassName(20)} onClick={() => this.handleItemsPerPage(20)}>20</a></li>
                            <li><a className={this.applyItemsPerPageClassName(30)} onClick={() => this.handleItemsPerPage(30)}>30</a></li>
                            <li><a className={this.applyItemsPerPageClassName(50)} onClick={() => this.handleItemsPerPage(50)}>50</a></li>
                        </ul>
                    </nav>
                </div>
                <div className="column is-9">
                    <p><strong>Номер страницы:</strong></p>
                    <nav className="pagination is-rounded" role="navigation" aria-label="pagination">
                        <ul className="pagination-list">
                            {back}
                            {paginationLinks}
                            {forward}
                        </ul>
                    </nav>
                </div>
            </div>
        )
    }

    render() {
        const cards = this.state.cards.map((card) => {
            const createdAt = createFullDateFromTimestamp(card.createdAt);
            const validTill = createDateFromTimestamp(card.validTill);
            const owner = `${card.carOwner.name.last} ${card.carOwner.name.first[0]}.${card.carOwner.name.middle[0]}.`;

            return (
                <CardTableItem
                    editable={Date.now() / 1000 - card.createdAt / 1000 <= 5 * 24 * 3600}
                    key={card.id}
                    id={card.id}
                    createdAt={createdAt}
                    car={`${card.carProperties.mark} ${card.carProperties.model}`}
                    plate={card.regDocProperties.plate}
                    vin={card.carProperties.vin}
                    owner={owner}
                    category={getCategoryCodes(card.carProperties.category, this.props.meta.categories)}
                    validTill={validTill}
                    eaistoNumber={card.eaistoNumber}
                />
            );
        });

        let stat = this.state.stat;

        if (this.state.showOnlyActive) {
            stat = stat.filter((statItem) => {
                return statItem.isActive === true;
            })
        }

        if (this.state.showOnlyWithDeals) {
            stat = stat.filter((statItem) => {
                return statItem.countA + statItem.countB + statItem.countC + statItem.countD + statItem.countE > 0;
            })
        }

        stat = stat.map((statItem) => {
            const sumTotal = statItem.priceA * statItem.countA + statItem.priceB * statItem.countB
                + statItem.priceC * statItem.countC + statItem.priceD * statItem.countD
                + statItem.priceE * statItem.countE;

            const countTotal = statItem.countA + statItem.countB + statItem.countC + statItem.countD + statItem.countE;

            return {
                name: statItem.name,
                login: statItem.login,
                countA: statItem.countA,
                countB: statItem.countB,
                countC: statItem.countC,
                countD: statItem.countD,
                countE: statItem.countE,
                countTotal: countTotal,
                sumTotal: sumTotal
            };

            // return (
            //     <StatTableItem
            //         key={statItem.login}
            //         userFullName={statItem.name}
            //         userLogin={statItem.login}
            //         countA={statItem.countA}
            //         countB={statItem.countB}
            //         countC={statItem.countC}
            //         countD={statItem.countD}
            //         countE={statItem.countE}
            //         countTotal={countTotal}
            //         sumTotal={sumTotal}
            //     />
            // )
        });

        let countTotalA = 0;
        let countTotalB = 0;
        let countTotalC = 0;
        let countTotalD = 0;
        let countTotalE = 0;
        let countTotal = 0;
        let sumTotal = 0;

        for (let i = 0; i < this.state.stat.length; i++) {
            countTotalA += this.state.stat[i].countA;
            countTotalB += this.state.stat[i].countB;
            countTotalC += this.state.stat[i].countC;
            countTotalD += this.state.stat[i].countD;
            countTotalE += this.state.stat[i].countE;

            countTotal = countTotal
                + this.state.stat[i].countA
                + this.state.stat[i].countB
                + this.state.stat[i].countC
                + this.state.stat[i].countD
                + this.state.stat[i].countE

            sumTotal = sumTotal
                + this.state.stat[i].countA * this.state.stat[i].priceA
                + this.state.stat[i].countB * this.state.stat[i].priceB
                + this.state.stat[i].countC * this.state.stat[i].priceC
                + this.state.stat[i].countD * this.state.stat[i].priceD
                + this.state.stat[i].countE * this.state.stat[i].priceE
        }

        const data = stat;

        const columns = [
            {
                Header: 'Имя',
                accessor: 'name',
                width: 195
            },
            {
                Header: 'Логин',
                accessor: 'login',
                Footer: (
                    <span><strong>Всего</strong></span>
                )
            },
            {
                Header: 'A',
                accessor: 'countA',
                Footer: (
                    <span><strong>{countTotalA}</strong></span>
                )
            },
            {
                Header: 'B',
                accessor: 'countB',
                Footer: (
                    <span><strong>{countTotalB}</strong></span>
                )
            },
            {
                Header: 'C',
                accessor: 'countC',
                Footer: (
                    <span><strong>{countTotalC}</strong></span>
                )
            },
            {
                Header: 'D',
                accessor: 'countD',
                Footer: (
                    <span><strong>{countTotalD}</strong></span>
                )
            },
            {
                Header: 'E',
                accessor: 'countE',
                Footer: (
                    <span><strong>{countTotalE}</strong></span>
                )
            },
            {
                Header: () => (<span>&sum;</span>),
                accessor: 'countTotal',
                Footer: (
                    <span><strong>{countTotal}</strong></span>
                )
            },
            {
                Header: () => (<span>&sum;<sub>&#8381;</sub></span>),
                accessor: 'sumTotal',
                Footer: (
                    <span><strong>{sumTotal}</strong></span>
                )
            }
        ];

        return (
            <div className="column is-9">
                <div className="level">
                    <span className="is-size-3">Выписанные карты:</span>
                </div>
                    <div className="columns">
                        <div className="column is-10">
                            <div className="columns">
                                <div className="column is-3">
                                    <p>
                                        <label for="card-date-from">
                                            <strong>Дата с:</strong>
                                        </label>
                                    </p>
                                    <p>
                                        <input
                                            className="input"
                                            type="date"
                                            id="card-date-from"
                                            onChange={(event) => this.handleDateFrom(event)}
                                        />
                                    </p>
                                </div>
                                <div className="column is-3">
                                    <p>
                                        <label for="card-date-to">
                                            <strong>Дата по:</strong>
                                        </label>
                                    </p>
                                    <p>
                                        <input
                                            className="input"
                                            type="date"
                                            id="card-date-to"
                                            onChange={(event) => this.handleDateTill(event)}
                                        />
                                    </p>
                                </div>
                                <div className="column is-6">
                                    <p>
                                        <label for="card-filter-agent">
                                            <strong>Агент</strong>
                                        </label>
                                    </p>
                                    <p>
                                        <Autocomplete
                                            items={this.state.agents}
                                            shouldItemRender={(item, value) => item.login.toLowerCase().indexOf(value.toLowerCase()) > -1}
                                            getItemValue={item => item.login}
                                            renderItem={(item, highlighted) =>
                                                <div
                                                    key={item.id}
                                                    style={{ backgroundColor: highlighted ? '#eee' : 'transparent'}}
                                                >
                                                    {item.login}
                                                </div>
                                            }
                                            inputProps={{
                                                className: 'input',
                                                id: 'card-filter-agent'
                                            }}
                                            menuStyle={
                                                {
                                                    background: 'rgba(255, 255, 255)',
                                                    border: '1px solid black'
                                                }
                                            }
                                            value={this.state.agentLoginFilter}
                                            onChange={e => this.setState({ agentLoginFilter: e.target.value })}
                                            onSelect={
                                                (value, item) => {
                                                    this.setState({
                                                        toUpdate: true,
                                                        agentLoginFilter: item.login,
                                                        agentIdFilter: item.id,
                                                        currentPage: 1
                                                    });
                                                }
                                            }
                                        />
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
                <div className="columns">
                    <div className="column is-4">
                        <label className="checkbox">
                            <input type="checkbox" checked={this.state.showOnlyActive} onChange={() => this.setState({showOnlyActive: !this.state.showOnlyActive})}/>
                            <strong>Скрыть заблокированных агентов ({this.countBannedUsers()})</strong>
                        </label>
                    </div>
                    <div className="column is-4">
                        <label className="checkbox">
                            <input type="checkbox" checked={this.state.showOnlyWithDeals} onChange={() => this.setState({showOnlyWithDeals: !this.state.showOnlyWithDeals})}/>
                            <strong>Скрыть нулевые ({this.countWithoutDealsUsers()})</strong>
                        </label>
                    </div>
                </div>
                <hr />
                <div className="level">
                    <ReactTable
                        className="-striped -highlight"
                        data={data}
                        columns={columns}
                        showPagination={false}
                        minRows={0}
                    />
                    {/*<table className="table is-fullwidth is-striped">*/}
                        {/*<thead>*/}
                        {/*<tr>*/}
                            {/*<th>Имя</th>*/}
                            {/*<th>Логин</th>*/}
                            {/*<th>A</th>*/}
                            {/*<th>B</th>*/}
                            {/*<th>C</th>*/}
                            {/*<th>D</th>*/}
                            {/*<th>E</th>*/}
                            {/*<th>&sum;</th>*/}
                            {/*<th>&sum;<sub>&#8381;</sub></th>*/}
                        {/*</tr>*/}
                        {/*</thead>*/}
                        {/*<tfoot>*/}
                        {/*<tr>*/}
                            {/*<th>*/}
                                {/*<a href="#">*/}
                                    {/*<span>*/}
                                      {/*<i className="fas fa-align-justify"></i>*/}
                                    {/*Отчет*/}
                                    {/*</span>*/}
                                {/*</a>*/}
                            {/*</th>*/}
                            {/*<th>Всего</th>*/}
                            {/*<th>{countTotalA}</th>*/}
                            {/*<th>{countTotalB}</th>*/}
                            {/*<th>{countTotalC}</th>*/}
                            {/*<th>{countTotalD}</th>*/}
                            {/*<th>{countTotalE}</th>*/}
                            {/*<th>{countTotal}</th>*/}
                            {/*<th>{sumTotal}</th>*/}
                        {/*</tr>*/}
                        {/*</tfoot>*/}
                        {/*<tbody>*/}
                            {/*{stat}*/}
                        {/*</tbody>*/}
                    {/*</table>*/}
                </div>
                <hr />
                <div className="level">
                    <table className="table is-fullwidth is-bordered is-striped">
                        <thead>
                        <tr>
                            <th></th>
                            <th>Дата, время</th>
                            <th>Марка, модель</th>
                            <th>Госномер, VIN</th>
                            <th>Собственник, категория</th>
                            <th>Дата окончания, #ЕАИСТО</th>
                        </tr>
                        </thead>
                        <tbody>
                            {cards}
                        </tbody>
                    </table>
                </div>
                    <div className="columns">
                        <div className="column is-12">
                            {this.showPagination()}
                        </div>
                    </div>
            </div>
        );
    }
}


