import React, { Component } from 'react';
import {Link} from 'react-router-dom';

export default class UserTableItem extends Component {
    handleUserStatus(status) {
        this.props.handleUserStatus(status);
    }

    render() {
        const tag = (this.props.isActive)
            ? <td><span className="tag is-success user-status" onClick={() => this.handleUserStatus(false)}>Работает</span></td>
            : <td><span className="tag is-danger user-status" onClick={() => this.handleUserStatus(true)}>Не работает</span></td>;

        return (
            <tr>
                <td>
                    <Link to={`/user/${this.props.id}`}>
                            <span>
                              <i className="fas fa-edit"></i>
                            </span>
                    </Link>
                </td>
                <td>{this.props.name}</td>
                <td>{this.props.login}</td>
                <td>{this.props.email}</td>
                <td>{this.props.priceA}/{this.props.priceB}/{this.props.priceC}/{this.props.priceD}/{this.props.priceE}</td>
                {tag}
            </tr>
        )
    }
}


