import React, { Component } from 'react';

export default class StatTableItem extends Component {
    render() {
        return (
            <tr>
                <td>{this.props.userFullName}</td>
                <td>{this.props.userLogin}</td>
                <td>{this.props.countA}</td>
                <td>{this.props.countB}</td>
                <td>{this.props.countC}</td>
                <td>{this.props.countD}</td>
                <td>{this.props.countE}</td>
                <td>{this.props.countTotal}</td>
                <td>{this.props.sumTotal}</td>
            </tr>
        );
    }
}


