export const normalizeDatePart = (part) => {
    part = Number(part);

    return part > 9 ? part : `0${part}`;
};

export const createFullDateFromTimestamp = (timestamp) => {
    return `${
        normalizeDatePart(timestamp.getDate())}.${normalizeDatePart(timestamp.getMonth() + 1)}.${normalizeDatePart(timestamp.getFullYear())} ${normalizeDatePart(timestamp.getHours())}:${normalizeDatePart(timestamp.getMinutes())}`;
};

export const createDateFromTimestamp = (timestamp) => {
    return `${
        normalizeDatePart(timestamp.getDate())}.${normalizeDatePart(timestamp.getMonth() + 1)}.${normalizeDatePart(timestamp.getFullYear())}`;
}

export const formatDateInputDashed = (timestamp) => {
    return `${normalizeDatePart(timestamp.getFullYear())}-${normalizeDatePart(timestamp.getMonth() + 1)}-${normalizeDatePart(timestamp.getDate())}`
}


