import React, { Component } from 'react';
import {NavLink} from 'react-router-dom';

export default class Nav extends Component {
    render() {
        return (
            <div className="column is-3">
                <nav className="panel">
                    <p className="panel-heading">
                        AutoCard
                    </p>
                    <NavLink className="panel-block" to="/stat" activeClassName="is-active">
                        <span className="panel-icon">
                          <i className="fas fa-align-justify"></i>
                        </span>
                        Карты
                    </NavLink>
                    <NavLink className="panel-block" to="/card" activeClassName="is-active">
                        <span className="panel-icon">
                          <i className="fas fa-calendar-plus"></i>
                        </span>
                        Добавить карту
                    </NavLink>
                    <NavLink className="panel-block" to="/users" activeClassName="is-active">
                        <span className="panel-icon">
                          <i className="fas fa-users"></i>
                        </span>
                        Агенты
                    </NavLink>
                    <NavLink className="panel-block" to="/limits" activeClassName="is-active">
                        <span className="panel-icon">
                          <i className="fas fa-sliders-h"></i>
                        </span>
                        Лимиты
                    </NavLink>
                    <NavLink className="panel-block" to="/settings" activeClassName="is-active">
                        <span className="panel-icon">
                          <i className="fas fa-cog"></i>
                        </span>
                        Настройки
                    </NavLink>
                    <a className="panel-block" onClick={() => this.props.logOut()}>
                        <span className="panel-icon">
                          <i className="fas fa-sign-out-alt"></i>
                        </span>
                        Выход
                    </a>
                </nav>
            </div>
        );
    }
}


