import React, { Component } from 'react';
import Axios from "axios/index";
import {Redirect} from 'react-router-dom';

export default class Settings extends Component {
    constructor(props) {
        super(props);

        this.state = {
            redirect: false,
            form: {
                passwordCurrent: {
                    value: '',
                    errors: []
                },
                passwordNew: {
                    value: '',
                    errors: []
                }
            }
        }
    }

    handleFormSubmit(event) {
        event.preventDefault();

        Axios.post(process.env.REACT_APP_API_HOST + 'api/changePassword?apiToken=' + this.props.apiToken, {
            passwordCurrent: this.state.form.passwordCurrent.value,
            passwordNew: this.state.form.passwordNew.value
        })
            .then((response) => {
                if (response.data.success) {
                    this.setState({
                        redirect: true
                    })
                } else {
                    for (let key in response.data.data.errors) {
                        this.setState({
                            form: {
                                ...this.state.form,
                                [key]: {
                                    value: '',
                                    errors: response.data.data.errors[key]
                                }
                            }
                        })
                    }
                }
            })
    }

    handleInput(event, fieldName) {
        this.setState({
            form: {
                ...this.state.form,
                [fieldName]: {
                    ...this.state.form[fieldName],
                    value: event.target.value,
                    errors: []
                }
            }
        })
    }

    applyInputClass(fieldName) {
        return (this.state.form[fieldName].errors.length > 0) ? 'input is-danger' : 'input';
    }

    showErrors(fieldName) {
        return this.state.form[fieldName].errors.map((error) => {
            return (
                <p className="help is-danger">{error}</p>
            )
        })
    }

    render() {
        if (this.state.redirect) {
            return (
                <Redirect to="/stat"/>
            )
        }

        return (
            <div className="column is-9">
                <div className="level">
                    <span className="is-size-3">Настройки</span>
                </div>
                <div className="columns">
                    <div className="column is-6">
                        <form onSubmit={(event) => this.handleFormSubmit(event)}>
                            <div className="field">
                                <label className="label">Текущий пароль</label>
                                <div className="control">
                                    <input
                                        value={this.state.form.passwordCurrent.value}
                                        onChange={(event) => this.handleInput(event, 'passwordCurrent')}
                                        className={this.applyInputClass('passwordCurrent')}
                                        type="password" />
                                </div>
                                {this.showErrors('passwordCurrent')}
                            </div>
                            <div className="field">
                                <label className="label">Новый пароль</label>
                                <div className="control">
                                    <input
                                        value={this.state.form.passwordNew.value}
                                        onChange={(event) => this.handleInput(event, 'passwordNew')}
                                        className={this.applyInputClass('passwordNew')}
                                        type="password" />
                                </div>
                                {this.showErrors('passwordNew')}
                            </div>
                            <div className="field is-grouped">
                                <div className="control">
                                    <button className="button is-link">Сохранить</button>
                                </div>
                                <div className="control">
                                    <button className="button is-text">Отмена</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}


