import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Axios from "axios/index";
import LimitTableItem from "./LimitTableItem";

export default class Limits extends Component {
    constructor(props) {
        super(props);

        this.state = {
            limits: []
        }
    }

    componentWillMount() {
        Axios.get(process.env.REACT_APP_API_HOST + 'api/limits?apiToken=' + this.props.apiToken)
            .then((response) => {
                if (response.data.success) {
                    const limits = response.data.data.limits.map((limit) => {
                        if (limit.isActive === '1') {
                            limit.isActive = true;
                        }

                        if (limit.isActive === '0') {
                            limit.isActive = false;
                        }

                        return limit;
                    });

                    this.setState({
                        limits: limits
                    })
                }
            })
    }

    showUsers() {
        return this.state.limits.map((limit) => {
            return (
                <LimitTableItem
                    handleLimitStatus={(isActive) => this.handleLimitStatus(limit.id, isActive)}
                    handleDeleteLimit={() => this.handleDeleteLimit(limit.id)}
                    isActive={limit.isActive}
                    name={limit.name}
                    categoryGroups={limit.categoryGroups}
                    limitation={limit.limitation}
                    countCards={limit.countCards}
                    left={Number(limit.limitation) - Number(limit.countCards)}
                    id={limit.id}
                    key={limit.id}/>
            )
        })
    }

    handleLimitStatus(id, isActive) {
        Axios.post(process.env.REACT_APP_API_HOST + 'api/limitStatus?apiToken=' + this.props.apiToken, {
            id: id,
            isActive: Number(isActive)
        })
            .then((response) => {
                if (response.data.success) {
                    const limits = this.state.limits.map((limit) => {
                        if (limit.id === id) {
                            limit.isActive = isActive;
                        }

                        return limit;
                    })

                    this.setState({
                        limits: limits
                    })
                }
            })
    }

    handleDeleteLimit(id) {
        Axios.post(process.env.REACT_APP_API_HOST + 'api/deleteLimit?apiToken=' + this.props.apiToken, {
            id: id
        })
            .then((response) => {
                if (response.data.success) {
                    const limits = this.state.limits.filter((limit) => {
                        return limit.id !== id
                    });

                    this.setState({
                        limits: limits
                    })
                }
            })
    }

    render() {
        return (
            <div className="column is-9">
                <div className="level">
                    <span className="is-size-3">Лимиты:</span>
                </div>
                <div className="columns">
                    <div className="column is-2">
                        <Link className="button is-primary" to="/limit">Добавить правило</Link>
                    </div>
                </div>
                <div className="columns">
                    <div className="column is-12">
                        <table className="table is-fullwidth is-striped">
                            <thead>
                            <tr>
                                <th></th>
                                <th>№</th>
                                <th>Наименование</th>
                                <th>Категории</th>
                                <th>Лимит</th>
                                <th>Сделано</th>
                                <th>Остаток</th>
                                <th>Период</th>
                                <th>Активность</th>
                                <th></th>
                            </tr>
                            </thead>
                            <tbody>
                                {this.showUsers()}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        )
    }
}


