import React, { Component } from 'react';
import Axios from "axios/index";
import Autocomplete from 'react-autocomplete';
import { Redirect } from 'react-router-dom';
import { formatDateInputDashed } from '../helpers/helperDateTime';

export default class EditCard extends Component {
    constructor(props) {
        super(props);

        this.state = {
            redirect: false,
            toUpdate: false,
            isEditing: false,
            cardId: null,
            canSave: true,
            unusualRegistrationNumber: false,
            unusualDocumentSeries: false,
            unusualDocumentNumber: false,
            operatorsCategories: [],
            carMarks: [],
            carModels: [],
            form: {
                documentType: {
                    value: 1,
                    errors: [],
                    messages: [],
                },
                documentSeries: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                documentNumber: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                propertyType: {
                    value: 1,
                    errors: [],
                    messages: [],
                },
                legalName: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                ownerLastName: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                ownerFirstName: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                ownerMiddleName: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                documentOrganization: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                documentDate: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                registrationNumber: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                vin: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                carMark: {
                    value: '',
                    id: null,
                    errors: [],
                    messages: [],
                },
                carModel: {
                    value: '',
                    id: null,
                    errors: [],
                    messages: [],
                },
                vehicleCategory: {
                    value: 2,
                    errors: [],
                    messages: [],
                },
                carYear: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                bodyNumber: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                frameNumber: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                maxMass: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                emptyMass: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                kilometres: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                tyres: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                operator: {
                    value: 1,
                    errors: [],
                    messages: [],
                },
                fuelType: {
                    value: 'Petrol',
                    errors: [],
                    messages: [],
                },
                brakingSystem: {
                    value: 'Mechanical',
                    errors: [],
                    messages: [],
                },
                note: {
                    value: '',
                    errors: [],
                    messages: [],
                },
                comment: {
                    value: '',
                    errors: [],
                    messages: [],
                }
            }
        }
    }

    documentTypes = {
        regTalon: 1,
        PTS: 2
    };

    propertyTypes = {
        own: 1,
        legal: 2
    };

    specialNotes = [
        'Нет ометок',
        'Исп. для ученой езды',
        'Исп. в качестве такси',
        'Исп. для маршрутных перевозок',
        'Исп. для опасных грузов',
        'Газобалонное оборудование'
    ];

    componentWillMount() {
        Axios.get(process.env.REACT_APP_API_HOST + 'api/carMarks?apiToken=' + this.props.apiToken)
            .then((response) => {
                this.setState({
                    carMarks: response.data.data.carMarks
                })
            });

        Axios.get(process.env.REACT_APP_API_HOST + 'api/operatorsCategories?apiToken=' + this.props.apiToken)
            .then((response) => {
                if (response.data.success) {
                    this.setState({
                        operatorsCategories: response.data.data.operatorsCategories
                    })
                }
            });

        if (this.props.cardId) {
            Axios.get(process.env.REACT_APP_API_HOST + 'api/cards/' + this.props.cardId + '?&apiToken=' + this.props.apiToken)
                .then((response) => {
                    if (response.data.success) {
                        this.setState({
                            isEditing: true,
                            cardId: response.data.data.card.id
                        });

                        for (let key in response.data.data.card) {
                            if (key === 'carMark' || key === 'carModel') {
                                this.setState({
                                    form: {
                                        ...this.state.form,
                                        [key]: {
                                            ...this.state.form[key],
                                            id: response.data.data.card[key].id,
                                            value: response.data.data.card[key].name,
                                        }
                                    }
                                })

                                if (key === 'carMark') {
                                    Axios.get(process.env.REACT_APP_API_HOST + 'api/carModels?markId=' + response.data.data.card[key].id + '&apiToken=' + this.props.apiToken)
                                        .then((response) => {
                                            this.setState({
                                                toUpdate: false,
                                                carModels: response.data.data.carModels
                                            })
                                        });
                                }

                            } else {

                                if (key === 'documentDate') {
                                    const date = new Date(response.data.data.card[key] * 1000);

                                    let day = date.getDate();
                                    let month = date.getMonth() + 1;
                                    let year = date.getFullYear();

                                    if (day <= 9) {
                                        day = `0${day}`;
                                    }

                                    if (month <= 9) {
                                        month = `0${month}`;
                                    }

                                    const dateFormatted = `${year}-${month}-${day}`;

                                    console.log(dateFormatted);

                                    this.setState({
                                        form: {
                                            ...this.state.form,
                                            [key]: {
                                                ...this.state.form[key],
                                                value: dateFormatted,
                                            }
                                        }
                                    })
                                } else {
                                    this.setState({
                                        form: {
                                            ...this.state.form,
                                            [key]: {
                                                ...this.state.form[key],
                                                value: response.data.data.card[key],
                                            }
                                        }
                                    })
                                }
                            }
                        }
                    }
                });
        }
    }

    componentDidUpdate() {
        if (this.state.toUpdate) {
            if (this.state.form.carMark.id === null) {
                return null;
            }

            Axios.get(process.env.REACT_APP_API_HOST + 'api/carModels?markId=' + this.state.form.carMark.id + '&apiToken=' + this.props.apiToken)
                .then((response) => {
                    this.setState({
                        toUpdate: false,
                        carModels: response.data.data.carModels
                    })
                })
        }
    }

    handleFormSubmit(event) {
        event.preventDefault();

        let data = {
            documentType: this.state.form.documentType.value,
            documentSeries: this.state.form.documentSeries.value,
            documentNumber: this.state.form.documentNumber.value,
            propertyType: this.state.form.propertyType.value,
            legalName: this.state.form.legalName.value,
            ownerLastName: this.state.form.ownerLastName.value,
            ownerFirstName: this.state.form.ownerFirstName.value,
            ownerMiddleName: this.state.form.ownerMiddleName.value,
            documentOrganization: this.state.form.documentOrganization.value,
            documentDate: new Date(this.state.form.documentDate.value).getTime() / 1000,
            registrationNumber: this.state.form.registrationNumber.value,
            vin: this.state.form.vin.value,
            carMark: this.state.form.carMark.id,
            carModel: this.state.form.carModel.id,
            carMarkName: this.state.form.carMark.value,
            carModelName: this.state.form.carModel.value,
            vehicleCategory: this.state.form.vehicleCategory.value,
            carYear: this.state.form.carYear.value,
            bodyNumber: this.state.form.bodyNumber.value,
            frameNumber: this.state.form.frameNumber.value,
            maxMass: this.state.form.maxMass.value,
            emptyMass: this.state.form.emptyMass.value,
            kilometres: this.state.form.kilometres.value,
            tyres: this.state.form.tyres.value,
            operator: this.state.form.operator.value,
            fuelType: this.state.form.fuelType.value,
            brakingSystem: this.state.form.brakingSystem.value,
            note: this.state.form.note.value,
            comment: this.state.form.comment.value,
            unusualRegistrationNumber: this.state.unusualRegistrationNumber,
            unusualDocumentSeries: this.state.unusualDocumentSeries,
            unusualDocumentNumber: this.state.unusualDocumentNumber
        };

        if (this.state.cardId !== null) {
            data['id'] = this.state.cardId
        }

        Axios.post(process.env.REACT_APP_API_HOST + 'api/cards?apiToken=' + this.props.apiToken, data).then((response) => {

            for (let key in this.state.form) {
                this.setState({
                    form: {
                        ...this.state.form,
                        [key]: {
                            ...this.state.form[key],
                            errors: []
                        }
                    }
                })
            }

            if (response.data.success) {
                this.setState({
                    redirect: true
                })
            } else {
                for (let key in response.data.data.errors) {
                    this.setState({
                        form: {
                            ...this.state.form,
                            [key]: {
                                ...this.state.form[key],
                                errors: response.data.data.errors[key]
                            }
                        }
                    })
                }
            }
        }).catch((error) => {
            console.log(error);
        })
    }

    handleInput(event, fieldName) {
        if (fieldName === 'carMark') {
            const match = Boolean(this.state.carMarks.filter((carMark) => {
                return carMark.name.toLowerCase().indexOf(event.target.value.toLowerCase()) !== -1;
            }).length);

            if (!match) {
                this.setState({
                    form: {
                        ...this.state.form,
                        carMark: {
                            ...this.state.form.carMark,
                            id: null,
                            value: event.target.value
                        }
                    }
                })
            } else {
                this.setState({
                    form: {
                        ...this.state.form,
                        carMark: {
                            ...this.state.form.carMark,
                            value: event.target.value
                        }
                    }
                })
            }
        } else if (fieldName === 'carModel') {
            const match = Boolean(this.state.carModels.filter((carModel) => {
                return carModel.name.toLowerCase().indexOf(event.target.value.toLowerCase()) !== -1;
            }).length);

            if (!match) {
                this.setState({
                    form: {
                        ...this.state.form,
                        carModel: {
                            ...this.state.form.carModel,
                            id: null,
                            value: event.target.value
                        }
                    }
                })
            } else {
                this.setState({
                    form: {
                        ...this.state.form,
                        carModel: {
                            ...this.state.form.carModel,
                            value: event.target.value
                        }
                    }
                })
            }
        } else if (fieldName === 'ownerFirstName' || fieldName === 'ownerLastName' || fieldName === 'ownerMiddleName') {
            this.setState({
                form: {
                    ...this.state.form,
                    [fieldName]: {
                        ...this.state.form[fieldName],
                        value: event.target.value.replace(/\d/g, ''),
                        errors: []
                    }
                }
            })
        } else {
            this.setState({
                form: {
                    ...this.state.form,
                    [fieldName]: {
                        ...this.state.form[fieldName],
                        value: event.target.value,
                        errors: []
                    }
                }
            })
        }

        if (fieldName === 'operator') {
            this.setState({
                form: {
                    ...this.state.form,
                    vehicleCategory: {
                        ...this.state.form.vehicleCategory,
                        value: 2,
                        errors: []
                    },
                    operator: {
                        ...this.state.form.operator,
                        value: event.target.value,
                        errors: []
                    }
                }
            })
        }

        if (fieldName === 'registrationNumber' || fieldName === 'vin') {
            this.setState({
                form: {
                    ...this.state.form,
                    [fieldName]: {
                        ...this.state.form[fieldName],
                        value: event.target.value.toUpperCase(),
                        errors: []
                    }
                }
            })
        }
    }

    setFieldToEmpty(fieldName) {
        this.setState({
            form: {
                ...this.state.form,
                [fieldName]: {
                    ...this.state.form[fieldName],
                    value: 'ОТСУТСТВУЕТ',
                    errors: []
                }
            }
        })
    }

    showErrors(fieldName) {
        return this.state.form[fieldName].errors.map((error) => {
            return (
                <p className="help is-danger">{error}</p>
            )
        })
    }

    applyInputClass(fieldName) {
        return (this.state.form[fieldName].errors.length > 0) ? 'input is-danger' : 'input';
    }

    applyTextareaClass(fieldName) {
        return (this.state.form[fieldName].errors.length > 0) ? 'textarea is-danger' : 'textarea';
    }

    render() {
        if (this.state.redirect) {
            return (
                <Redirect to="/stat"/>
            )
        }

        const legalNameField = (this.state.form.propertyType.value == 2) ?
            (
                <div className="field">
                    <label className="label">Наименование</label>
                    <div className="control">
                        <textarea
                            value={this.state.form.legalName.value}
                            onChange={(event) => this.handleInput(event, 'legalName')}
                            className="textarea"
                            rows="2" />
                    </div>
                </div>
            ) : null;

        const carModelField = (this.state.form.carMark.id === null) ? (
            <div className="field">
                <label className="label">Модель</label>
                <div className="control">
                    <input
                        value={this.state.form.carModel.value}
                        onChange={(event) => this.handleInput(event, 'carModel')}
                        className={this.applyInputClass('carModel')}
                        type="text" />
                </div>
                {this.showErrors('carModel')}
            </div>
        ) : (
            <div className="field">
                <label className="label">Модель</label>
                <div className="control">
                    {/*<input*/}
                    {/*value={this.state.form.carMark.value}*/}
                    {/*onChange={(event) => this.handleInput(event, 'carMark')}*/}
                    {/*className={this.applyInputClass('carMark')}*/}
                    {/*type="text" />*/}
                    <Autocomplete
                        items={this.state.carModels}
                        shouldItemRender={(item, value) => item.name.toLowerCase().indexOf(value.toLowerCase()) > -1}
                        getItemValue={item => item.name}
                        renderItem={(item, highlighted) =>
                            <div
                                key={item.id}
                                style={{ backgroundColor: highlighted ? '#eee' : 'transparent'}}
                            >
                                {item.name}
                            </div>
                        }
                        menuStyle={
                            {
                                background: 'rgba(255, 255, 255)',
                                border: '1px solid black'
                            }
                        }
                        inputProps={{
                            className: this.applyInputClass('carModel')
                        }}
                        wrapperStyle={
                            {
                                display: 'block'
                            }
                        }
                        value={this.state.form.carModel.value}
                        onChange={(event) => this.handleInput(event, 'carModel')}
                        onSelect={
                            (value, item) => {
                                this.setState({
                                    form: {
                                        ...this.state.form,
                                        carModel: {
                                            ...this.state.form.carModel,
                                            value: item.name,
                                            id: item.id
                                        }
                                    }
                                });
                            }
                        }
                    />
                </div>
                {this.showErrors('carMark')}
            </div>
        );

        const vehicleCategoryOptions = this.props.meta.categories.map((category) => {
            const currentOperatorChosen = this.state.form.operator.value;

            const currentOperatorAllowedCategories = this.state.operatorsCategories.filter((operatorCategory) => {
                return currentOperatorChosen == operatorCategory.operatorId;
            }).map((operatorCategory) => {
                return operatorCategory.categoryId;
            });

            if (currentOperatorAllowedCategories.indexOf(category.id) !== -1) {
                return (
                    <option key={category.id} value={category.id}>{`${category.letter} (${category.code}) - ${category.description}`}</option>
                )
            }

        });

        const operatorOptions = this.props.meta.operators.map((operator) => {
            return (
                <option key={operator.id} value={operator.id}>{operator.shortName}</option>
            )
        });

        const fuelTypeOptions = this.props.meta.fuelTypes.map((fuelType) => {
            return (
                <option key={fuelType.Code} value={fuelType.Code}>{fuelType.Title}</option>
            )
        });

        const brakingSystemOptions = this.props.meta.brakeTypes.map((brakeType) => {
            return (
                <option key={brakeType.Code} value={brakeType.Code}>{brakeType.Title}</option>
            )
        });

        const noteOptions = this.specialNotes.map((note, index) => {
            return (
                <option key={index} value={note}>{note}</option>
            )
        });

        return (
            <div className="column is-9">
                <div className="level">
                    <span className="is-size-3">{(this.state.isEditing) ? 'Редактировать карту' : 'Выписать карту'}</span>
                </div>
                <form onSubmit={(event) => this.handleFormSubmit(event)}>
                    <div className="columns">
                        <div className="column is-12">
                            <div className="columns">
                                <div className="column is-6">
                                    <div className="field">
                                        <label className="label">Тип документа</label>
                                        <div className="control">
                                            <label className="radio">
                                                <input
                                                    type="radio"
                                                    name="documentType"
                                                    checked={this.state.form.documentType.value == this.documentTypes.regTalon}
                                                    onClick={(event) => this.handleInput(event, 'documentType')}
                                                    value={this.documentTypes.regTalon} />
                                                    СРТС
                                            </label>
                                            <label className="radio">
                                                <input
                                                    type="radio"
                                                    name="documentType"
                                                    checked={this.state.form.documentType.value == this.documentTypes.PTS}
                                                    onClick={(event) => this.handleInput(event, 'documentType')}
                                                    value={this.documentTypes.PTS}/>
                                                    ПТС
                                            </label>
                                        </div>
                                        {this.showErrors('documentType')}
                                    </div>
                                    <div className="field">
                                        <label className="label">Серия (Нестандартная <input type="checkbox" checked={this.state.unusualDocumentSeries} onChange={() => this.setState({unusualDocumentSeries: !this.state.unusualDocumentSeries})}/>)</label>
                                        <div className="control">
                                            <input
                                                value={this.state.form.documentSeries.value}
                                                onChange={(event) => this.handleInput(event, 'documentSeries')}
                                                className={this.applyInputClass('documentSeries')}
                                                type="text" />
                                        </div>
                                        {this.showErrors('documentSeries')}
                                    </div>
                                    <div className="field">
                                        <label className="label">Номер (Нестандартный <input type="checkbox" checked={this.state.unusualDocumentNumber} onChange={() => this.setState({unusualDocumentNumber: !this.state.unusualDocumentNumber})}/>)</label>
                                        <div className="control">
                                            <input
                                                value={this.state.form.documentNumber.value}
                                                onChange={(event) => this.handleInput(event, 'documentNumber')}
                                                className={this.applyInputClass('documentNumber')}
                                                type="text" />
                                            {this.showErrors('documentNumber')}
                                        </div>
                                    </div>
                                    <div className="field">
                                        <label className="label">Тип собственника</label>
                                        <div className="control">
                                            <label className="radio">
                                                <input
                                                    value={this.propertyTypes.own}
                                                    onChange={(event) => this.handleInput(event, 'propertyType')}
                                                    checked={this.state.form.propertyType.value == this.propertyTypes.own}
                                                    type="radio"
                                                    name="propertyType" />
                                                    Физ. лицо
                                            </label>
                                            <label className="radio">
                                                <input
                                                    value={this.propertyTypes.legal}
                                                    onChange={(event) => this.handleInput(event, 'propertyType')}
                                                    checked={this.state.form.propertyType.value == this.propertyTypes.legal}
                                                    type="radio"
                                                    name="propertyType" />
                                                    Юр. лицо
                                            </label>
                                            {this.showErrors('propertyType')}
                                        </div>
                                    </div>
                                    {legalNameField}
                                    <div className="field">
                                        <label className="label">Фамилия</label>
                                        <div className="control">
                                            <input
                                                value={this.state.form.ownerLastName.value}
                                                onChange={(event) => this.handleInput(event, 'ownerLastName')}
                                                className={this.applyInputClass('ownerLastName')}
                                                type="text" />
                                        </div>
                                        {this.showErrors('ownerLastName')}
                                    </div>
                                    <div className="field">
                                        <label className="label">Имя</label>
                                        <div className="control">
                                            <input
                                                value={this.state.form.ownerFirstName.value}
                                                onChange={(event) => this.handleInput(event, 'ownerFirstName')}
                                                className={this.applyInputClass('ownerFirstName')}
                                                type="text" />
                                        </div>
                                        {this.showErrors('ownerFirstName')}
                                    </div>
                                    <div className="field">
                                        <label className="label">Отчество</label>
                                        <div className="field has-addons">
                                            <div className="control is-expanded">
                                                <input
                                                    value={this.state.form.ownerMiddleName.value}
                                                    onChange={(event) => this.handleInput(event, 'ownerMiddleName')}
                                                    className={this.applyInputClass('ownerMiddleName')}
                                                    type="text" />
                                            </div>
                                            <div className="control">
                                                <a
                                                    onClick={() => this.setFieldToEmpty('ownerMiddleName')}
                                                    className="button is-info">
                                                    Отсутствует
                                                </a>
                                            </div>
                                        </div>
                                        {this.showErrors('ownerMiddleName')}
                                    </div>
                                    <div className="field">
                                        <label className="label">Кем выдан</label>
                                        <div className="control">
                                            <textarea
                                                value={this.state.form.documentOrganization.value}
                                                onChange={(event) => this.handleInput(event, 'documentOrganization')}
                                                className={this.applyTextareaClass('documentOrganization')}
                                                rows="2" />
                                        </div>
                                        {this.showErrors('documentOrganization')}
                                    </div>
                                    <div className="field">
                                        <label className="label">Дата выдачи</label>
                                        <div className="control">
                                            <input
                                                value={this.state.form.documentDate.value}
                                                onChange={(event) => this.handleInput(event, 'documentDate')}
                                                className={this.applyInputClass('documentDate')}
                                                min="1700-01-01"
                                                max={formatDateInputDashed(new Date())}
                                                type="date" />
                                        </div>
                                        {this.showErrors('documentDate')}
                                    </div>
                                </div>
                                <div className="column is-6">
                                    <div className="field">
                                        <label className="label">Рег. знак (Нестандартный <input type="checkbox" checked={this.state.unusualRegistrationNumber} onChange={() => this.setState({unusualRegistrationNumber: !this.state.unusualRegistrationNumber})}/>)</label>
                                        <div className="field has-addons">
                                            <div className="control is-expanded">
                                                <input
                                                    value={this.state.form.registrationNumber.value}
                                                    onChange={(event) => this.handleInput(event, 'registrationNumber')}
                                                    className={this.applyInputClass('registrationNumber')}
                                                    type="text" />
                                            </div>
                                            <div className="control">
                                                <a
                                                    onClick={() => this.setFieldToEmpty('registrationNumber')}
                                                    className="button is-info">
                                                    Отсутствует
                                                </a>
                                            </div>
                                        </div>
                                        {this.showErrors('registrationNumber')}
                                    </div>
                                    <div className="field">
                                        <label className="label">VIN номер</label>
                                        <div className="field has-addons">
                                            <div className="control is-expanded">
                                                <input
                                                    value={this.state.form.vin.value}
                                                    onChange={(event) => this.handleInput(event, 'vin')}
                                                    className={this.applyInputClass('vin')}
                                                    type="text" />
                                            </div>
                                            <div className="control">
                                                <a
                                                    onClick={() => this.setFieldToEmpty('vin')}
                                                    className="button is-info">
                                                    Отсутствует
                                                </a>
                                            </div>
                                        </div>
                                        {this.showErrors('vin')}
                                    </div>
                                    <div className="field">
                                        <label className="label">Марка</label>
                                        <div className="control">
                                            {/*<input*/}
                                                {/*value={this.state.form.carMark.value}*/}
                                                {/*onChange={(event) => this.handleInput(event, 'carMark')}*/}
                                                {/*className={this.applyInputClass('carMark')}*/}
                                                {/*type="text" />*/}
                                            <Autocomplete
                                                items={this.state.carMarks}
                                                shouldItemRender={(item, value) => item.name.toLowerCase().indexOf(value.toLowerCase()) > -1}
                                                menuStyle={
                                                    {
                                                        background: 'rgba(255, 255, 255)',
                                                        border: '1px solid black'
                                                    }
                                                }
                                                renderInput={(props) => {
                                                    return (<input {...props} />)
                                                }}
                                                getItemValue={item => item.name}
                                                renderItem={(item, highlighted) =>
                                                    <div
                                                        key={item.id}
                                                        style={{ backgroundColor: highlighted ? '#eee' : 'transparent'}}
                                                    >
                                                        {item.name}
                                                    </div>
                                                }
                                                wrapperStyle={
                                                    {
                                                        display: 'block'
                                                    }
                                                }
                                                inputProps={{
                                                    className: this.applyInputClass('carMark')
                                                }}
                                                value={this.state.form.carMark.value}
                                                onChange={(event) => this.handleInput(event, 'carMark')}
                                                onSelect={
                                                    (value, item) => {
                                                        this.setState({
                                                            toUpdate: true,
                                                            form: {
                                                                ...this.state.form,
                                                                carMark: {
                                                                    ...this.state.form.carMark,
                                                                    value: item.name,
                                                                    id: item.id
                                                                }
                                                            }
                                                        });
                                                    }
                                                }
                                            />
                                        </div>
                                        {this.showErrors('carMark')}
                                    </div>

                                    {carModelField}

                                    <div className="field">
                                        <label className="label">Категория ТС</label>
                                        <div className="control">
                                            <div className="select is-fullwidth">
                                                <select
                                                    className={this.applyInputClass('vehicleCategory')}
                                                    value={this.state.form.vehicleCategory.value}
                                                    onChange={(event) => this.handleInput(event, 'vehicleCategory')}>
                                                    {vehicleCategoryOptions}
                                                </select>
                                            </div>
                                        </div>
                                        {this.showErrors('vehicleCategory')}
                                    </div>
                                    <div className="field">
                                        <label className="label">Год выпуска</label>
                                        <div className="control">
                                            <input
                                                value={this.state.form.carYear.value}
                                                onChange={(event) => this.handleInput(event, 'carYear')}
                                                className={this.applyInputClass('carYear')}
                                                type="text" />
                                        </div>
                                        {this.showErrors('carYear')}
                                    </div>
                                    <div className="field">
                                        <label className="label">№ рамы (шасси)</label>
                                        <div className="field has-addons">
                                            <div className="control is-expanded">
                                                <input
                                                    value={this.state.form.frameNumber.value}
                                                    onChange={(event) => this.handleInput(event, 'frameNumber')}
                                                    className={this.applyInputClass('frameNumber')}
                                                    type="text" />
                                            </div>
                                            <div className="control">
                                                <a
                                                    onClick={() => this.setFieldToEmpty('frameNumber')}
                                                    className="button is-info">
                                                    Отсутствует
                                                </a>
                                            </div>
                                        </div>
                                        {this.showErrors('frameNumber')}
                                    </div>
                                    <div className="field">
                                        <label className="label">№ кузова</label>
                                        <div className="field has-addons">
                                            <div className="control is-expanded">
                                                <input
                                                    value={this.state.form.bodyNumber.value}
                                                    onChange={(event) => this.handleInput(event, 'bodyNumber')}
                                                    className={this.applyInputClass('bodyNumber')}
                                                    type="text" />
                                            </div>
                                            <div className="control">
                                                <a
                                                    onClick={() => this.setFieldToEmpty('bodyNumber')}
                                                    className="button is-info">
                                                    Отсутствует
                                                </a>
                                            </div>
                                        </div>
                                        {this.showErrors('bodyNumber')}
                                    </div>
                                    <div className="field">
                                        <label className="label">Разр. max масса</label>
                                        <div className="control">
                                            <input
                                                value={this.state.form.maxMass.value}
                                                onChange={(event) => this.handleInput(event, 'maxMass')}
                                                className={this.applyInputClass('maxMass')}
                                                type="text" />
                                        </div>
                                        {this.showErrors('maxMass')}
                                    </div>
                                    <div className="field">
                                        <label className="label">Масса без нагр.</label>
                                        <div className="control">
                                            <input
                                                value={this.state.form.emptyMass.value}
                                                onChange={(event) => this.handleInput(event, 'emptyMass')}
                                                className={this.applyInputClass('emptyMass')}
                                                type="text" />
                                        </div>
                                        {this.showErrors('emptyMass')}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <hr />
                    <div className="columns">
                        <div className="column is-6">
                            <div className="field">
                                <label className="label">Пробег</label>
                                <div className="control">
                                    <input
                                        value={this.state.form.kilometres.value}
                                        onChange={(event) => this.handleInput(event, 'kilometres')}
                                        className={this.applyInputClass('kilometres')}
                                        type="text" />
                                </div>
                                {this.showErrors('kilometres')}
                            </div>
                            <div className="field">
                                <label className="label">Марка шин</label>
                                <div className="control">
                                    <Autocomplete
                                        items={this.props.meta.tyres}
                                        shouldItemRender={(item, value) => item.toLowerCase().indexOf(value.toLowerCase()) > -1}
                                        menuStyle={
                                            {
                                                background: 'rgba(255, 255, 255)',
                                                border: '1px solid black'
                                            }
                                        }
                                        renderInput={(props) => {
                                            return (<input {...props} />)
                                        }}
                                        getItemValue={item => item}
                                        renderItem={(item, highlighted) =>
                                            <div
                                                key={item}
                                                style={{ backgroundColor: highlighted ? '#eee' : 'transparent'}}
                                            >
                                                {item}
                                            </div>
                                        }
                                        wrapperStyle={
                                            {
                                                display: 'block'
                                            }
                                        }
                                        inputProps={{
                                            className: this.applyInputClass('tyres')
                                        }}
                                        value={this.state.form.tyres.value}
                                        onChange={(event) => this.handleInput(event, 'tyres')}
                                        onSelect={
                                            (value, item) => {
                                                this.setState({
                                                    toUpdate: true,
                                                    form: {
                                                        ...this.state.form,
                                                        tyres: {
                                                            ...this.state.form.tyres,
                                                            value: item
                                                        }
                                                    }
                                                });
                                            }
                                        }
                                    />
                                </div>
                                {this.showErrors('tyres')}
                            </div>
                            <div className="field">
                                <label className="label">Пункт ТО</label>
                                <div className="control">
                                    <div className="select">
                                        <select
                                            className={this.applyInputClass('operator')}
                                            value={this.state.form.operator.value}
                                            onChange={(event) => this.handleInput(event, 'operator')}>
                                            {operatorOptions}
                                        </select>
                                    </div>
                                </div>
                                {this.showErrors('operator')}
                            </div>
                        </div>
                        <div className="column is-6">
                            <div className="field">
                                <label className="label">Тип топлива</label>
                                <div className="control">
                                    <div className="select is-fullwidth">
                                        <select
                                            className={this.applyInputClass('fuelType')}
                                            value={this.state.form.fuelType.value}
                                            onChange={(event) => this.handleInput(event, 'fuelType')}>
                                            {fuelTypeOptions}
                                        </select>
                                    </div>
                                </div>
                                {this.showErrors('fuelType')}
                            </div>
                            <div className="field">
                                <label className="label">Тип тормозов</label>
                                <div className="control">
                                    <div className="select is-fullwidth">
                                        <select
                                            className={this.applyInputClass('brakingSystem')}
                                            value={this.state.form.brakingSystem.value}
                                            onChange={(event) => this.handleInput(event, 'brakingSystem')}>
                                            {brakingSystemOptions}
                                        </select>
                                    </div>
                                </div>
                                {this.showErrors('brakingSystem')}
                            </div>
                            <div className="field">
                                <label className="label">Особые отметки</label>
                                <div className="control">
                                    <div className="select is-fullwidth">
                                        <select
                                            value={this.state.form.note.value}
                                            onChange={(event) => this.handleInput(event, 'note')}>
                                            {noteOptions}
                                        </select>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="columns">
                        <div className="column is-12">
                            <div className="field">
                                <label className="label">Комментарий</label>
                                <div className="control">
                                    <input
                                        value={this.state.form.comment.value}
                                        onChange={(event) => this.handleInput(event, 'comment')}
                                        className="input"
                                        type="text" />
                                </div>
                            </div>
                            <div className="field is-grouped">
                                <div className="control">
                                    <button className="button is-link">Сохранить</button>
                                </div>
                                <div className="control">
                                    <button className="button is-text">Отмена</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </form>
            </div>
        );
    }
}


