import React, { Component } from 'react';
import {Route} from 'react-router-dom';
import './App.css';
import Nav from "./components/Nav";
import Cards from "./components/Cards";
import EditCard from "./components/EditCard";
import Axios from 'axios';
import Users from "./components/Users";
import EditUser from "./components/EditUser";
import Settings from "./components/Settings";
import EditLimit from "./components/EditLimit";
import Limits from "./components/Limits";

class App extends Component {
    constructor(props) {
        super(props);

        this.state = {
            apiToken: null,
            toUpdate: false,
            currentUser: [],
            limits: [],
            meta: {
                fuelTypes: [],
                brakeTypes: [],
                operators: [],
                categories: [],
                printForms: [],
                tyres: [],
            },
            authentication: {
                login: '',
                password: ''
            }
        }
    }

    logOut() {
        localStorage.removeItem('authorization');

        this.setState({
            apiToken: null
        })
    }

    handleAuthenticationInputChange(event, fieldName) {
        this.setState({
            authentication: {
                ...this.state.authentication,
                [fieldName]: event.target.value
            }
        })
    }

    handleAuthenticationFormSubmit(event) {
        event.preventDefault();

        Axios.post(process.env.REACT_APP_API_HOST + 'authentication/login', {
            login: this.state.authentication.login,
            password: this.state.authentication.password
        })
            .then((response) => {
                if (response.data.success) {
                    this.setState({
                        apiToken: response.data.data.apiToken,
                        toUpdate: true
                    });

                    localStorage.setItem('authorization', response.data.data.apiToken);
                } else {
                    alert(response.data.errors[0]);
                }
            })
            .catch((error) => {
                console.log(error);
            })
    }

    componentWillUpdate(nextProps, nextState) {
        if (nextState.toUpdate) {
            if (nextState.apiToken) {
                console.log('did update');
                this.loadMeta(nextState.apiToken);

                this.setState({
                    toUpdate: false
                })
            }
        }
    }

    componentWillMount() {
        const apiToken = (localStorage.getItem('authorization')) ? localStorage.getItem('authorization') : null;

        if (!apiToken) {
            this.setState({
                apiToken: null
            })
        } else {
            this.setState({
                apiToken: apiToken
            });

            this.loadMeta(apiToken);
        }
    }

    loadMeta(apiToken) {
        Axios.get(process.env.REACT_APP_API_HOST + 'api/categories?apiToken=' + apiToken)
            .then((response) => {
                this.setState({
                    meta: {
                        ...this.state.meta,
                        categories: response.data.data.categories
                    }
                })
            });

        Axios.get(process.env.REACT_APP_API_HOST + 'api/fuel?apiToken=' + apiToken)
            .then((response) => {
                this.setState({
                    meta: {
                        ...this.state.meta,
                        fuelTypes: response.data.data.fuelTypes
                    }
                })
            });

        Axios.get(process.env.REACT_APP_API_HOST + 'api/brakes?apiToken=' + apiToken)
            .then((response) => {
                this.setState({
                    meta: {
                        ...this.state.meta,
                        brakeTypes: response.data.data.brakeTypes
                    }
                })
            });

        Axios.get(process.env.REACT_APP_API_HOST + 'api/operators?apiToken=' + apiToken)
            .then((response) => {
                this.setState({
                    meta: {
                        ...this.state.meta,
                        operators: response.data.data.operators
                    }
                })
            });

        Axios.get(process.env.REACT_APP_API_HOST + 'api/tyres?apiToken=' + apiToken)
            .then((response) => {
                this.setState({
                    meta: {
                        ...this.state.meta,
                        tyres: response.data.data.tyres
                    }
                })
            });
    }

    render() {
        if (this.state.apiToken === null) {
            return (
                <div className="container has-text-centered container-login">
                    <form onSubmit={(event) => this.handleAuthenticationFormSubmit(event)}>
                        <div className="field has-text-left">
                            <label className="label">Имя пользователя</label>
                            <div className="control">
                                <input className="input" type="text" onChange={(event) => this.handleAuthenticationInputChange(event, 'login')} />
                            </div>
                        </div>

                        <div className="field has-text-left">
                            <label className="label">Пароль</label>
                            <div className="control">
                                <input className="input" type="password" onChange={(event) => this.handleAuthenticationInputChange(event, 'password')} />
                            </div>
                        </div>
                        <div className="field is-grouped">
                            <div className="control">
                                <button className="button is-link">Войти</button>
                            </div>
                        </div>
                    </form>
                </div>
            )
        }

        return (
            <div className="container">
                <div className="columns">
                    <Nav logOut={() => this.logOut()}/>
                    <Route exact path="/" render={() => <Cards meta={this.state.meta} apiToken={this.state.apiToken} currentUser={this.state.currentUser} />}/>
                    <Route exact path="/stat" render={() => <Cards meta={this.state.meta} apiToken={this.state.apiToken} currentUser={this.state.currentUser} />}/>
                    <Route exact path="/card" render={() => <EditCard meta={this.state.meta} apiToken={this.state.apiToken} currentUser={this.state.currentUser} />} />
                    <Route exact path="/card/:id" render={(matches) =>
                        <EditCard
                            cardId={matches.match.params.id}
                            meta={this.state.meta}
                            apiToken={this.state.apiToken}
                            currentUser={this.state.currentUser} />} />
                    <Route exact path="/users" render={() => <Users meta={this.state.meta} apiToken={this.state.apiToken} currentUser={this.state.currentUser} />}/>
                    <Route exact path="/user" render={() => <EditUser meta={this.state.meta} apiToken={this.state.apiToken} currentUser={this.state.currentUser} />} />
                    <Route exact path="/user/:id" render={(matches) =>
                        <EditUser
                            userId={matches.match.params.id}
                            meta={this.state.meta}
                            apiToken={this.state.apiToken}
                            currentUser={this.state.currentUser} />} />
                    <Route exact path="/settings" render={() => <Settings meta={this.state.meta} apiToken={this.state.apiToken} currentUser={this.state.currentUser} />}/>
                    <Route exact path="/limits" render={() => <Limits meta={this.state.meta} apiToken={this.state.apiToken} currentUser={this.state.currentUser} />} />
                    <Route exact path="/limit" render={() => <EditLimit meta={this.state.meta} apiToken={this.state.apiToken} currentUser={this.state.currentUser} />}/>
                    <Route exact path="/limit/:id" render={(matches) =>
                        <EditLimit
                            limitId={matches.match.params.id}
                            meta={this.state.meta}
                            apiToken={this.state.apiToken}
                            currentUser={this.state.currentUser} />}/>
                </div>
            </div>
        );
  }
}

export default App;



