import React, { Component } from 'react';
import {Link} from 'react-router-dom';

export default class LimitTableItem extends Component {
    handleLimitStatus(status) {
        this.props.handleLimitStatus(status);
    }

    render() {
        const tag = (this.props.isActive)
            ? <td><span className="tag is-success user-status" onClick={() => this.handleLimitStatus(false)}>Работает</span></td>
            : <td><span className="tag is-danger user-status" onClick={() => this.handleLimitStatus(true)}>Не работает</span></td>;

        return (
            <tr>
                <td>
                    <Link to={`/limit/${this.props.id}`}>
                            <span>
                              <i className="fas fa-edit"></i>
                            </span>
                    </Link>
                </td>
                <td>1</td>
                <td>{this.props.name}</td>
                <td>{this.props.categoryGroups}</td>
                <td>{this.props.limitation}</td>
                <td>{this.props.countCards}</td>
                <td>{this.props.left}</td>
                <td>День</td>
                {tag}
                <td>

                <a onClick={() => this.props.handleDeleteLimit()}>
                    <span>
                      <i className="fas fa-trash-alt"></i>
                    </span>
                </a>

                </td>
            </tr>
        )
    }
}


