import React, { Component } from 'react';
import Axios from "axios/index";
import {Redirect} from 'react-router-dom';

export default class EditLimit extends Component {
    days = [
        'Пн',
        'Вт',
        'Ср',
        'Чт',
        'Пт',
        'Сб',
        'Вс'
    ];

    constructor(props) {
        super(props);

        this.state = {
            redirect: false,
            isEditing: false,
            limitId: null,
            workingHours: [],
            categoryGroups: [],
            operators: [],
            users: [],
            form: {
                name: {
                    value: '',
                    errors: []
                },
                categoryGroups: {
                    value: -1,
                    list: []
                },
                operators: {
                    value: -1,
                    list: []
                },
                users: {
                    value: -1,
                    list: []
                },
                limit: {
                    value: ''
                }
            }
        }
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.meta.operators.length > 0) {
            this.setOperators(nextProps);
        }
    }

    componentDidMount() {
        this.setOperators(this.props);
    }

    setOperators(props) {
        const operators = props.meta.operators.map((operator) => {
            return {
                id: operator.id,
                title: operator.shortName,
                show: true
            }
        });

        this.setState({
            operators: operators
        })
    }

    componentWillMount() {
        Axios.get(process.env.REACT_APP_API_HOST + 'api/categoryGroups?apiToken=' + this.props.apiToken)
            .then((response) => {
                if (response.data.success) {
                    const categoryGroups = response.data.data.categoryGroups.map((group) => {
                        return {
                            id: group.letter,
                            title: group.letterDescription,
                            show: true
                        }
                    });

                    this.setState({
                        categoryGroups: categoryGroups
                    })
                }
            });

        Axios.get(process.env.REACT_APP_API_HOST + 'api/agents?excludeCurrent=1&apiToken=' + this.props.apiToken)
            .then((response) => {
                if (response.data.success) {
                    const users = response.data.data.agents.filter((user) => {
                        return (user.isActive === '1');
                    }).map((user) => {
                        return {
                            id: user.id,
                            title: `${user.name} (${user.login})`,
                            show: true
                        }
                    });

                    this.setState({
                        users: users
                    })
                }
            });

        if (this.props.limitId) {

            this.setState({
                isEditing: true,
                limitId: this.props.limitId
            });

            Axios.get(process.env.REACT_APP_API_HOST + 'api/limits/' + this.props.limitId + '?apiToken=' + this.props.apiToken)
                .then((response) => {
                    if (response.data.success) {
                        this.setState({
                            ...this.state,
                            workingHours: response.data.data.limit.workingHours,
                            form: {
                                ...this.state.form,
                                name: {
                                    ...this.state.form.name,
                                    value: response.data.data.limit.name
                                },
                                operators: {
                                    ...this.state.form.operators,
                                    list: response.data.data.limit.operators
                                },
                                categoryGroups: {
                                    ...this.state.form.categoryGroups,
                                    list: response.data.data.limit.categoryGroups
                                },
                                users: {
                                    ...this.state.form.users,
                                    list: response.data.data.limit.users
                                },
                                limit: {
                                    ...this.state.form.limit,
                                    value: response.data.data.limit.limitation
                                }
                            }
                        });

                        this.hideTaggedOptions('categoryGroups');
                        this.hideTaggedOptions('operators');
                        this.hideTaggedOptions('users');
                    }
                })

        }
    }

    hideTaggedOptions(entityType) {
        const idsToHide = this.state.form[entityType].list.map((entity) => {
            return entity.id;
        });

        const entitiesList = this.state[entityType].map((entity) => {
            if (idsToHide.indexOf(entity.id) !== -1) {
                entity.show = false;
            }

            return entity;
        });

        this.setState({
            [entityType]: entitiesList
        })
    }

    hours = () => {
        let hours = [];

        for (let hour = 0; hour <= 24; hour++) {
            hours.push(hour);
        }

        return hours;
    };

    handleFormSubmit(event) {
        event.preventDefault();

        let data = {
            name: this.state.form.name.value,
            limit: this.state.form.limit.value,
            categoryGroups: this.state.form.categoryGroups.list,
            operators: this.state.form.operators.list,
            users: this.state.form.users.list,
            workingHours: this.state.workingHours
        };

        if (this.state.limitId !== null) {
            data['id'] = this.state.limitId
        }

        Axios.post(process.env.REACT_APP_API_HOST + 'api/limits?&apiToken=' + this.props.apiToken, data)
            .then((response) => {
                if (response.data.success) {
                    this.setState({
                        redirect: true
                    })
                }
            })
    }

    handleAddTagged(event, entityType) {
        if (event.target.value !== '0' && event.target.value !== '-1') {
            const entity = this.state[entityType].filter((entity) => {
                return entity.id === event.target.value
            })[0];

            let chosenEntities = this.state.form[entityType].list;

            chosenEntities.push(entity);

            this.setState({
                form: {
                    ...this.state.form,
                    [entityType]: {
                        ...this.state.form[entityType],
                        list: chosenEntities
                    }
                }
            });

            let entitiesList = this.state[entityType].map((entity) => {
                if (entity.id === event.target.value) {
                    entity.show = false;
                }

                return entity;
            });

            this.setState({
                [entityType]: entitiesList
            });
        } else if (event.target.value === '0') {
            let chosenEntities = this.state[entityType].map((entity) => {
                return {
                    id: entity.id,
                    title: entity.title
                }
            });

            this.setState({
                form: {
                    ...this.state.form,
                    [entityType]: {
                        ...this.state.form[entityType],
                        list: chosenEntities
                    }
                }
            });

            let entitiesList = this.state[entityType].map((entity) => {
                entity.show = false;

                return entity;
            });

            this.setState({
                [entityType]: entitiesList
            });
        }
    }

    handleDeleteTagged(id, entityType) {
        const chosenEntities = this.state.form[entityType].list.filter((entity) => {
            return entity.id !== id;
        });

        this.setState({
            form: {
                ...this.state.form,
                [entityType]: {
                    ...this.state.form[entityType],
                    list: chosenEntities
                }
            }
        });

        let entitiesList = this.state[entityType].map((entity) => {
            if (entity.id === id) {
                entity.show = true;
            }

            return entity;
        });

        this.setState({
            [entityType]: entitiesList
        });
    }

    checkIfAllTaggedChosen(entityType) {
        return this.state[entityType].length === this.state.form[entityType].list.length;
    }

    showEntityTags(entityType) {
        return this.state.form[entityType].list.map((entity) => {
            const entityTitle = (entityType === 'categoryGroups') ? `${entity.id} - ${entity.title}` : entity.title;

            return (
                <div className="control">
                    <div className="tags has-addons">
                        <a className="tag is-link">{entityTitle}</a>
                        <a className="tag is-delete" onClick={() => this.handleDeleteTagged(entity.id, entityType)}></a>
                    </div>
                </div>
            )
        });
    }

    showLimitInput() {
        return (this.state.form.limit.value === '')
            ? <input
                onChange={(event) =>
                    this.setState({
                        form: {
                            ...this.state.form,
                            limit: {
                                ...this.state.form.limit,
                                value: event.target.value
                            }
                        }
                    })}
                className="input"
                type="text"
                placeholder="Лимит отсутствует"
                value={this.state.form.limit.value} />
            : <input
                onChange={(event) =>
                    this.setState({
                        form: {
                            ...this.state.form,
                            limit: {
                                ...this.state.form.limit,
                                value: event.target.value
                            }
                        }
                    })}
                className="input"
                type="text"
                value={this.state.form.limit.value} />;
    }

    changeLimit(addition) {
        this.setState({
            form: {
                ...this.state.form,
                limit: {
                    ...this.state.form.limit,
                    value: (Number(this.state.form.limit.value) + addition)
                }
            }
        })
    }

    showEntityOptions(entityType) {
        return this.state[entityType].map((entity) => {
            if (entity.show) {
                return (entityType === 'categoryGroups')
                    ? <option key={entity.id} value={entity.id}>{entity.id} - {entity.title}</option>
                    : <option key={entity.id} value={entity.id}>{entity.title}</option>;

            }

        });
    }

    checkIsWorking(dayChecking, hourChecking) {
        for (let i = 0; i < this.state.workingHours.length; i++) {
            if (this.state.workingHours[i].day === dayChecking && this.state.workingHours[i].hour === hourChecking) {
                return true;
            }
        }

        return false;
    }

    handleIsWorking(day, hour, isWorking) {
        if (isWorking) {
            const workingHours = this.state.workingHours.filter((workingHour) => {
                return workingHour.day !== day || workingHour.hour !== hour;
            });

            this.setState({
                workingHours: workingHours
            });
        } else {
            let workingHours = this.state.workingHours;

            workingHours.push({
                day: day,
                hour: hour
            });

            this.setState({
                workingHours: workingHours
            });
        }
    }

    handleFullDayWorking(day, isFullDayWorking) {
        let workingHours = this.state.workingHours.filter((workingHour) => {
            return workingHour.day !== day;
        });

        this.setState({
            workingHours: workingHours
        });

        if (!isFullDayWorking) {
            for (let i = 0; i <=23; i++) {
                workingHours.push({
                    day: day,
                    hour: i
                })
            }

            this.setState({
                workingHours: workingHours
            })
        }
    }

    checkIsFullDayWorking(day) {
        let counter = 0;

        for (let i = 0; i < this.state.workingHours.length; i++) {
            if (this.state.workingHours[i].day === day) {
                counter++;
            }
        }

        return counter === 24;
    }

    handleFullHourWorking(hour, isFullHourWorking) {
        let workingHours = this.state.workingHours.filter((workingHour) => {
            return workingHour.hour !== hour;
        });

        this.setState({
            workingHours: workingHours
        });

        if (!isFullHourWorking) {
            for (let i = 0; i <=6; i++) {
                workingHours.push({
                    day: i,
                    hour: hour
                })
            }

            this.setState({
                workingHours: workingHours
            })
        }
    }

    setAllWorkingDays() {
        let workingHours = [];

        for (let dayKey = 0; dayKey <= 6; dayKey++) {
            for (let hourKey = 0; hourKey <=23; hourKey++ ) {
                if (dayKey >= 0 && dayKey <= 4) {
                    workingHours.push({
                        day: dayKey,
                        hour: hourKey
                    })
                }
            }
        }

        this.setState({
            workingHours: workingHours
        })
    }

    setAllWeekends() {
        let workingHours = [];

        for (let dayKey = 0; dayKey <= 6; dayKey++) {
            for (let hourKey = 0; hourKey <=23; hourKey++ ) {
                if (dayKey > 4) {
                    workingHours.push({
                        day: dayKey,
                        hour: hourKey
                    })
                }
            }
        }

        this.setState({
            workingHours: workingHours
        })
    }

    setAllDays() {
        let workingHours = [];

        for (let dayKey = 0; dayKey <= 6; dayKey++) {
            for (let hourKey = 0; hourKey <=23; hourKey++ ) {
                workingHours.push({
                    day: dayKey,
                    hour: hourKey
                })
            }
        }

        this.setState({
            workingHours: workingHours
        })
    }

    setCommonWorkingHours() {
        let workingHours = [];

        for (let dayKey = 0; dayKey <= 6; dayKey++) {
            for (let hourKey = 0; hourKey <=23; hourKey++ ) {
                if (dayKey >= 0 && dayKey <= 4) {
                    if (hourKey >= 8 && hourKey <= 17) {
                        workingHours.push({
                            day: dayKey,
                            hour: hourKey
                        })
                    }
                }
            }
        }

        this.setState({
            workingHours: workingHours
        })
    }

    setCommonNotWorkingHours() {
        let workingHours = [];

        for (let dayKey = 0; dayKey <= 6; dayKey++) {
            for (let hourKey = 0; hourKey <=23; hourKey++ ) {
                if (dayKey >= 0 && dayKey <= 4) {
                    if (hourKey < 8 || hourKey > 17) {
                        workingHours.push({
                            day: dayKey,
                            hour: hourKey
                        })
                    }
                }

                if (dayKey > 4) {
                    workingHours.push({
                        day: dayKey,
                        hour: hourKey
                    })
                }
            }
        }

        this.setState({
            workingHours: workingHours
        })
    }

    checkIsFullHourWorking(hour) {
        let counter = 0;

        for (let i = 0; i < this.state.workingHours.length; i++) {
            if (this.state.workingHours[i].hour === hour) {
                counter++;
            }
        }

        return counter === 7;
    }

    render() {
        if (this.state.redirect) {
            return (
                <Redirect to="/limits"/>
            )
        }

        const tableHours = this.days.map((day, index) => {
            const hours = this.hours().map((hour) => {
               if (hour === 0) {
                   const isFullDayWorking = this.checkIsFullDayWorking(index);

                   return (
                       <td onClick={() => this.handleFullDayWorking(index, isFullDayWorking)} key={hour} className={isFullDayWorking ? 'is-selected' : null}>
                           {day}
                       </td>
                   )
               }

               const isWorking = this.checkIsWorking(index, hour - 1);

                return (
                   <td onClick={() => this.handleIsWorking(index, hour - 1, isWorking)} key={hour} className={isWorking ? 'is-selected' : null}>

                   </td>
               )
            });

            return (
                <tr key={index}>
                    {hours}
                </tr>
            )
        });

        const tableHeaderHours = this.hours().map((hour) => {
            if (hour === 0) {
                return (
                    <td>
                        День
                    </td>
                )
            }

            const isFullHourWorking = this.checkIsFullHourWorking(hour - 1);

            return (
                <td onClick={() => this.handleFullHourWorking(hour - 1, isFullHourWorking)} className={isFullHourWorking ? 'is-selected' : null}>
                    {hour - 1 <= 9 ? `0${hour - 1}` : hour - 1}
                </td>
            )
        });

        return (
            <div className="column is-9">
                <div className="level">
                    <span className="is-size-3">Редактировать правило</span>
                </div>
                <div className="columns">
                    <div className="column is-9">
                        <form onSubmit={(event) => this.handleFormSubmit(event)}>
                            <div className="field">
                                <label className="label">Наименование</label>
                                <div className="control">
                                    <input
                                        value={this.state.form.name.value}
                                        onChange={(event) => {
                                            this.setState({
                                                form: {
                                                    ...this.state.form,
                                                    name: {
                                                        ...this.state.form.name,
                                                        value: event.target.value
                                                    }
                                                }
                                            })
                                        }}
                                        className="input"
                                        type="text"/>
                                </div>
                            </div>
                            <div className="field">
                                <a className="button" onClick={() => {this.setState({workingHours: []})}}>Очистить</a>
                                <a className="button" onClick={() => this.setCommonWorkingHours()}>Рабочие часы</a>
                                <a className="button" onClick={() => this.setCommonNotWorkingHours()}>Нерабочие часы</a>
                                <a className="button" onClick={() => this.setAllWorkingDays()}>Рабочие дни</a>
                                <a className="button" onClick={() => this.setAllWeekends()}>Выходные</a>
                                <a className="button" onClick={() => this.setAllDays()}>Все дни</a>
                                <table className="table is-striped is-fullwidth is-bordered">
                                    <thead>
                                    <tr>
                                        {tableHeaderHours}
                                    </tr>
                                    </thead>
                                    <tbody>
                                    {tableHours}
                                    </tbody>
                                </table>
                            </div>
                            <div className="field">
                                <label className="label">Категории</label>
                                <div className="control">
                                    <div className="select is-fullwidth">
                                        <select
                                            onChange={(event) => this.handleAddTagged(event, 'categoryGroups')}
                                            className="input"
                                            value={this.state.form.categoryGroups.value}>
                                                <option value="-1">Выбрать...</option>
                                                {this.checkIfAllTaggedChosen('categoryGroups') ? null : <option value="0">Все</option>}
                                                {this.showEntityOptions('categoryGroups')}
                                        </select>
                                    </div>
                                </div>
                            </div>
                            <div className="field is-grouped is-grouped-multiline">
                                {this.showEntityTags('categoryGroups')}
                            </div>
                            <div className="field">
                                <label className="label">СТО</label>
                                <div className="control">
                                    <div className="select is-fullwidth">
                                        <select
                                            onChange={(event) => this.handleAddTagged(event, 'operators')}
                                            className="input"
                                            value={this.state.form.operators.value}>
                                            <option value="-1">Выбрать...</option>
                                            {this.checkIfAllTaggedChosen('operators') ? null : <option value="0">Все</option>}
                                            {this.showEntityOptions('operators')}
                                        </select>
                                    </div>
                                </div>
                            </div>
                            <div className="field is-grouped is-grouped-multiline">
                                {this.showEntityTags('operators')}
                            </div>
                            <div className="field">
                                <label className="label">Агенты</label>
                                <div className="control">
                                    <div className="select is-fullwidth">
                                        <select
                                            onChange={(event) => this.handleAddTagged(event, 'users')}
                                            className="input"
                                            value={this.state.form.users.value}>
                                            <option value="-1">Выбрать...</option>
                                            {this.checkIfAllTaggedChosen('users') ? null : <option value="0">Все</option>}
                                            {this.showEntityOptions('users')}
                                        </select>
                                    </div>
                                </div>
                            </div>
                            <div className="field is-grouped is-grouped-multiline">
                                {this.showEntityTags('users')}
                            </div>
                            <div className="field">
                                <label className="label">Лимит</label>
                                <div className="field has-addons">
                                    <div className="control">
                                        <a className="button" onClick={() => this.changeLimit(-5)}>
                                            -5
                                        </a>
                                    </div>
                                    <div className="control">
                                        <a className="button" onClick={() => this.changeLimit(-1)}>
                                            -1
                                        </a>
                                    </div>
                                    <div className="control is-expanded">
                                        {this.showLimitInput()}
                                    </div>
                                    <div className="control">
                                        <a className="button" onClick={() => this.setState({
                                            form: {
                                                ...this.state.form,
                                                limit: {
                                                    ...this.state.form.limit,
                                                    value: ''
                                                }
                                            }
                                        })}>
                                            Очистить
                                        </a>
                                    </div>
                                    <div className="control">
                                        <a className="button" onClick={() => this.setState({
                                            form: {
                                                ...this.state.form,
                                                limit: {
                                                    ...this.state.form.limit,
                                                    value: 0
                                                }
                                            }
                                        })}>
                                            0
                                        </a>
                                    </div>
                                    <div className="control">
                                        <a className="button" onClick={() => this.changeLimit(1)}>
                                            +1
                                        </a>
                                    </div>
                                    <div className="control">
                                        <a className="button" onClick={() => this.changeLimit(5)}>
                                            +5
                                        </a>
                                    </div>
                                </div>
                            </div>
                            <div className="field is-grouped">
                                <div className="control">
                                    <button className="button is-link">Сохранить</button>
                                </div>
                                <div className="control">
                                    <button className="button is-text">Отмена</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}


